.title-text {
    font-style: "Ubuntu, non-serif";
    font-weight: 900;
    font-size: 50px;
    -webkit-animation: texclip 1.5s linear infinite;
            animation: texclip 1.5s linear infinite;
    background: linear-gradient(to right,#034f84 10%, #667292 50%, #45c983 60%);
    background-clip: border-box;
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: textclip 10s linear infinite;
            animation: textclip 10s linear infinite;
    display: inline-block;
    margin-top: 50px;
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

